import { useEffect, useState } from "react";
import "./Mid.css";
import BigArticleMid from "../components/BigArticleMid";
import LatestNews from "../components/LatestNews";
import CardWithPic from "../components/CardWithPic";
import BottomLeftArticle from "../components/BottomLeftArticle";
import MidBarArticle from "../components/MidBarArticle";
import CategoryIcon from "../components/CategoryIcon";

function Mid() {
  var [articles, setArticles] = useState([]);
  var [cards, setCards] = useState([]);
  var [bottomArticles, setBottomArticles] = useState([]);
  var [midBarArticles, setMidBarArticles] = useState([]);
  var [categoryIcons, setCategoryIcons] = useState([]);
  

  
  useEffect(function() {
    fetch("./database.json")
    .then(function(response) {
      return response.json();
    })
    .then(function(data) {
      setArticles(data.bigArticlesMid);
      setCards(data.cardsWPic);
      setBottomArticles(data.bottomLeftArticles);
      setMidBarArticles(data.midBarArticles);
      setCategoryIcons(data.categoryIcon);
    })
  }, []);

  return (
    <section className="middleSection">
      <section className="bigArticles">
        {/*  artikel kald */}
        {articles.map(function (article) {
          return <BigArticleMid key={ article.heading } article={ article } />;
        })}
        {/* mid bar artikel kald */}
        <div className="bigArticles__midBar">
          {midBarArticles.map(function (article) {
            return <MidBarArticle key={ article.title } article={ article } />;
          })}
        </div>
        { /*cards kald.*/}
        <div className="cardWithPic">
          {cards.map(function (card) {
            return <CardWithPic key={ card.title } card={ card } />;
          })}
        </div>
        <div className="bigArticles__icons">
          {/* category icon kald. */}
          {categoryIcons.map(function (icon) {
              return <CategoryIcon key={ icon.title } icon={ icon } />;
            })}
        </div>
      </section>
      <div className="rightSide">
        <LatestNews />
        {/*artikel kald*/}
        {bottomArticles.map(function (article) {
          return <BottomLeftArticle key={ article.articleID } article={ article } />;
        })}
      </div>
    </section>
  )
}

export default Mid;