import "./MidBarArticle.css";

function MidBarArticle({article}) {
  return (
    <div className="midBarArticle">
      <img className="midBarArticle__img" src={article.images[0].src} alt=""/>
      <div>
        <h1 style={{color: article.colorcode}} className="midBarArticle__heading">{article.title}</h1>
        <p className="midBarArticle__text">{article.text}</p>
      </div>
    </div>
  )
}

export default MidBarArticle;