import "./CategoryIcon.css";

function CategoryIcon({icon}) {
  return (
    <div style={{backgroundColor: icon.colorcode}} className="middleIcon">
      <img className="middleIcon__icon" src={icon.images[0].src} alt=""/>
      <h1 className="middleIcon__title">{icon.title}</h1>
    
    </div>

  )
}

export default CategoryIcon;