import "./BottomLeftArticle.css";

function BottomLeftArticle({article}) {

  return (
    <article className="bottomLeftArticle">
      <div>
        <img className="bottomLeftArticle__icon" src={article.images[0].src} alt=""/>
        <h1 className="bottomLeftArticle__heading">{article.title}</h1>
      </div>
      <p className="bottomLeftArticle__text">{article.text}</p>
      <button className="bottomLeftArticle__button">Read more</button>
    </article>
  )
}

export default BottomLeftArticle;